@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap);
:root {
	--systemBlue: rgb(0, 122, 255);
	--systemBrown: rgb(162, 132, 94);
	--systemCyan: rgb(50, 173, 230);
	--systemGreen: rgb(52, 199, 89);
	--systemIndigo: rgb(88, 86, 214);
	--systemMint: rgb(0, 199, 190);
	--systemOrange: rgb(255, 149, 0);
	--systemPink: rgb(255, 45, 85);
	--systemPurple: rgb(175, 82, 222);
	--systemRed: rgb(255, 59, 48);
	--systemTeal: rgb(48, 176, 199);
	--systemYellow: rgb(255, 204, 0);


	--Skeleton_main: #f2f3f5;
	--Skeleton_secondary: #e9ebec;
	--Skeleton_main: rgba(174, 183, 194, .12);
	--Skeleton_secondary: rgba(174, 183, 194, .12);
}
[scheme=space_gray] {
	--systemBlue: rgb(10, 132, 255);
	--systemBrown: rgb(172, 142, 104);
	--systemCyan: rgb(100, 210, 255);
	--systemGreen: rgb(48, 209, 88);
	--systemIndigo: rgb(94, 92, 230);
	--systemMint: rgb(102, 212, 207);
	--systemOrange: rgb(255, 159, 10);
	--systemPink: rgb(255, 55, 95);
	--systemPurple: rgb(191, 90, 242);
	--systemRed: rgb(255, 69, 58);
	--systemTeal: rgb(64, 200, 224);
	--systemYellow: rgb(255, 214, 10);

	
	--Skeleton_main: #222223;
	--Skeleton_secondary: #353536;
	--Skeleton_main: rgba(93, 95, 97, .12);
	--Skeleton_secondary: rgba(93, 95, 97, .12);
}
@font-face {
	font-family: "VKSansDisplay-Medium";
	src: url(../../static/media/VKSansDisplay-Medium.5dae08a3.ttf);
}
@font-face {
	font-family: "VKSansDisplay-DemiBold";
	src: url(../../static/media/VKSansDisplay-DemiBold.35d21771.ttf);
}
@font-face {
	font-family: "VKSansDisplay-Bold";
	src: url(../../static/media/VKSansDisplay-Bold.50b10c44.ttf);
}

.vkuiSimpleCell--sizeY-compact .vkuiSimpleCell__description, .vkuiSimpleCell--sizeY-compact>.vkuiAvatar--sz-32~.vkuiSimpleCell__main .vkuiSimpleCell__description, .vkuiSimpleCell--sizeY-compact>.vkuiAvatar--sz-28~.vkuiSimpleCell__main .vkuiSimpleCell__description {
    margin-top: 3px;
    display: block;
}
.Spacing--separator {
	margin-bottom: 1px;
}

.PanelHeader__fixed {
	background: var(--header_background);
}
.PanelHeader--sizeX-compact .PanelHeader__separator {
	opacity: 0;
}
.FixedLayout:after {
	content: '';
	display: block;
	margin-top: 4px;
	/* margin-left: 12px;
	margin-right: 12px; */
	height: 1px;
	background: var(--separator_common);
	-webkit-transform-origin: center bottom;
	        transform-origin: center bottom;
}
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
	.FixedLayout:after {
		-webkit-transform: scaleY(.5);
		        transform: scaleY(.5);
	}
}
@media (-webkit-min-device-pixel-ratio: 3), (min-resolution: 3dppx) {
	.FixedLayout:after {
		-webkit-transform: scaleY(.33);
		        transform: scaleY(.33);
	}
}

body, .ModalPage__content {
	overflow-y: overlay;
}
body::-webkit-scrollbar, .ModalPage__content::-webkit-scrollbar {
	width: 0;
}
.Placeholder__icon:first-child:last-child {
	margin: 0!important;
}
.vkuiLink .vkuiIcon {
	margin: 0!important;
}
.vkuiGroup--card .vkuiCardGrid {
	padding: 0!important;
	margin: 0!important;
}
.FromGroup .Header__in {
	padding: 0!important;
}
.FromGroup .Header__main {
	margin: 0 0 8px 0!important;
}
div.Scroll {
	position: relative;
	overflow-y: overlay;
}
.vkuiCard {
	overflow: hidden;
	-webkit-transform: translate3d(0, 0, 0);
}
.vkuiHorizontalScroll {
	overflow: visible;
}
.CardWithAvatar .SimpleCell__description {
	margin-top: 3px;
	text-overflow: ellipsis;
	display: block;
}
.CardWithAvatar > .Card__in {
	overflow: hidden;
	-webkit-transform: translate3d(0, 0, 0);
}
.CardWithAvatar .vkuiAvatar {
	padding-top: 0;
	padding-bottom: 0;
}
.CardWithAvatar .Avatar {
	border-radius: 0!important;
}
.CardWithAvatar.Card--android .Avatar--shadow.Avatar::after {
	border-radius: 8px 0 0 8px;
}
.CardWithAvatar.Card--ios .Avatar--shadow.Avatar::after {
	border-radius: 10px 0 0 10px;
}
.CardWithAvatar .vkuiText {
	font-weight: 500;
}
.DescriptionCardWiki.withColor {
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}
.DescriptionCardWiki:not(.withColor) {
	background: transparent;
}
.DescriptionCardWiki.withColor .SimpleCell {
	padding: 16px;
}
.DescriptionCardWiki.withColor .Cell__marker {
	display: none;
}

.DescriptionCardWiki.MiniCell .SimpleCell__main {
	display: flex;
	flex-direction: column-reverse;
}
.DescriptionCardWiki.MiniCell .Icon {
	padding: 9px 7px 9px 0;
}
.DescriptionCardWiki.MiniCell .SimpleCell__main {
	padding: 0;
}
.DescriptionCardWiki.MiniCell .SimpleCell__children {
	font-size: 13px;
	line-height: 16px;
}
.DescriptionCardWiki.MiniCell .SimpleCell__description {
	text-transform: uppercase;
	font-weight: 700;
	font-size: 9px;
	line-height: 9px;
}


.DescriptionWiki .vkuiSimpleCell {
	white-space: pre-wrap;
}
.CardWithAvatar.itemPreview .Avatar {
	height: 71px!important;
	width: 67px!important;
	background: transparent;
}
.CardWithAvatar.itemPreview .Avatar::after {
	display: none;
}
.DescriptionCell i,
.DescriptionRichCell i {
	width: 16px;
	height: 16px;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	position: relative;
	display: inline-flex;
	margin-left: 5px;
	margin-bottom: -2px;
}
.vkuiPanelHeader__right {
	min-width: auto!important;
}
.ModalWiki .vkuiModalPageHeader__content {
	justify-content: center;
}
.ModalPageHeader:not(:empty) {
	position: relative;
}
.ModalPageHeader:not(:empty):after {
	content: '';
	position: absolute;
	width: 100%;
	height: 1px;
	left: 0;
	bottom: 0;
	background-color: var(--separator_common);
	-webkit-transform-origin: center bottom;
	        transform-origin: center bottom;
}
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
	.ModalPageHeader:not(:empty):after {
		-webkit-transform: scaleY(.5);
		        transform: scaleY(.5);
	}
}
@media (-webkit-min-device-pixel-ratio: 3), (min-resolution: 3dppx) {
	.ModalPageHeader:not(:empty):after {
		-webkit-transform: scaleY(.33);
		        transform: scaleY(.33);
	}
}
.ModalPage__content .Group__inner {
	padding-top: unset;
	padding-bottom: 12px;
}
.ModalPage__content .Spacing + .FormItem {
	margin-top: -12px;
}
.itemAvatarPreloadWiki, .bannerPreloadWiki, .cardAvatarPreloadWiki {
	background: var(--content_tint_background);
	position: absolute;
}
.cardAvatarPreloadWiki {
	background: transparent;
	width: 67px!important;
	height: 71px!important;
	left: 0;
}
.CardWithAvatar:not(.ForInput) .cardAvatarPreloadWiki.Head {
	width: 72px!important;
	height: 72px!important;
}
.itemAvatarPreloadWiki {
	width: 60px!important;
	height: 60px!important;
	margin: 15.5px 3.5px;
	border-radius: 10px;
}
.itemPricePreloadWiki {
	width: 24px;
	height: 24px;
	position: absolute;
}
.itemAvatarWiki {
	width: 67px;
	height: 71px;
	margin-top: 10px;
	margin-bottom: 10px;
	margin-right: 10px;
	position: relative;
}
.itemTextWiki {
	color: var(--text_secondary);
	font-weight: 500;
	font-size: 12px;
	width: 40px;
	text-align: right;
	display: inline-block;
}
.itemDescriptionWiki {
	color: var(--text_secondary);
	color: var(--panel_tab_active_text);
	background: var(--content_tint_background);
	background-color: var(--panel_tab_active_background);
	font-weight: 500;
	font-size: 12px;
	padding: 2px 10px;
	border-radius: 10px;
	display: inline-block;
}
.itemTypeWiki, .itemPriceWiki {
	display: flex;
	align-items: center;
	justify-content: flex-end;
}
.itemPriceWiki {
	margin-bottom: 10px;
	margin-left: 10px;
	color: var(--text_primary);
	font-weight: 500;
}
.itemPriceWiki i {
	width: 24px;
	height: 24px;
	margin-left: 5px;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	position: relative;
}
.itemCellWiki .vkuiSimpleCell {
	align-items: flex-start;
}
.itemCellWiki .vkuiSimpleCell__after {
	padding-top: 8px;
}
.BannerWiki .vkuiCard__in {
	overflow: hidden;
	-webkit-transform: translate3d(0, 0, 0);
}
.BannerWiki .vkuiCard__in::before {
	content: '';
	width: 100%;
	height: 100%;
	display: block;
	position: absolute;
	background: linear-gradient(0deg, rgba(0,0,0,.75) 0%, rgba(0,0,0,0) 100%);
	z-index: 1;
}
.BannerWiki .vkuiCell {
	background-position: center;
}
.BannerWiki:not(.Scroll) .vkuiCell {
	background-size: cover;
}
.BannerWiki .vkuiSimpleCell {
	display: block;
	z-index: 1;
	/* padding: 0; */
}
.BannerWiki.Form .SimpleCell__main {
	padding: 0;
}
.BannerWiki.Form .FormItem__top > div > span {
	color: var(--button_commerce_foreground);
	margin-left: 10px;
	overflow: hidden;
	-webkit-transform: translate3d(0, 0, 0);
	text-overflow: ellipsis;
}
.BannerWiki.Form .Cell {
	background-size: cover!important;
}
.BannerWiki.Form .Card__in::before {
	backdrop-filter: blur(10px);
	-webkit-backdrop-filter: blur(10px);
}
.BannerWiki:not(.Form) .vkuiSimpleCell {
	padding-top: 40%;
}
.CardGrid--s:not(.size-x4) .BannerWiki:not(.Form) .vkuiSimpleCell {
	padding-top: 30%;
}
.BannerWiki .vkuiText, .BannerWiki .vkuiTitle, .BannerWiki .vkuiHeadline {
	color: white!important;
}
.headBannerWiki {
	width: 100%;
	height: 100%;
	background-size: cover;
	background-position: center;
	position: relative;
}
.GradientBannerWiki .vkuiHeader__main {
	margin: 0 0 8px 0!important;
}
.HeadBannerWiki .bannerPreloadWiki, .HeadBannerWiki .vkuiBanner__bg, .HeadBannerWiki .LabelBannerWiki {
	border-radius: inherit;
}
.HeadBannerWiki .LabelBannerWiki {
	background-size: cover;
	background-position: center;
	position: relative;
}
.HeadBannerWiki.toLeft .LabelBannerWiki {
	background-position: right;
}
.HeadBannerWiki.toRight .LabelBannerWiki {
	background-position: left;
}
.HeadBannerWiki.toLeft {
	text-align: left;
}
.HeadBannerWiki.toRight {
	text-align: right;
}
.HeadBannerWiki.toRight .vkuiBanner__actions >.vkuiButton {
	margin-left: 16px;
	margin-right: 0!important;
}
.HeadBannerWiki .Banner__subheader {
	white-space: pre;
}
.Tabs {
	margin: 0!important;
	padding: 0!important;
}
div.Scroll::-webkit-scrollbar {
	width: 6px;
}
div.Scroll::-webkit-scrollbar-thumb:vertical {
	/* border: 6px solid rgba(0, 0, 0, 0); */
	/* background-clip: padding-box; */
	border-radius: 12px;
	background-color: rgba(0,0,0,0.28);
}
[scheme=space_gray] div.Scroll::-webkit-scrollbar-thumb:vertical {
	background-color: rgba(255,255,255,0.28);
}
div.Scroll::-webkit-scrollbar-thumb:vertical:hover {
	background-color: rgba(0,0,0,0.56);
}
[scheme=space_gray] div.Scroll::-webkit-scrollbar-thumb:vertical:hover {
	background-color: rgba(255,255,255,0.56);
}
div.Scroll::-webkit-scrollbar-track {
	background: transparent;
}
.itemPreview {
	overflow: hidden;
	-webkit-transform: translate3d(0, 0, 0);
}
.itemPreview .Avatar__shadow, .itemPreview .Avatar--shadow.Avatar::after {
	display: none;
}

.Checkbox .FormItem {
	padding: 0;
}
.Checkbox .Checkbox {
	width: calc(100% - 32px);
	margin: 0;
}

.BannerBoss.Banner {
	padding: 0;
	margin: 0;
}
.BannerBoss .Banner__in {
	padding: 16px 0 0 16px;
}
.BannerBoss .Banner__subheader {
	margin: 4px 0 8px 0;
}
.BannerBoss .vkuiTitle--l-2 {
	line-height: 16px;
}
.BannerBoss .CardGrid {
	padding: 0!important;
}
.BannerBoss .Card {
	background: #e8d1bb;
	border-radius: 0px;
	box-shadow: 0 0 0 1px #cab29b, 0 0 0 2px #ecd5bd, inset 0 0 0 1px #ecd5bd, inset 0 0 0 2px #cab29b;
}

.BannerBoss .SimpleCell {
	border-radius: 0;
}
.BannerBoss .withAvatar .SimpleCell {
	padding-left: 8px;
	padding-right: 12px;
}
.BannerBoss .withAvatar .Avatar {
	font-weight: 500;
	line-height: 24px;
	margin: 0 8px 0 0!important;
}
.BannerBoss .withAvatar .Avatar .Avatar__children {
	color: #7b6137;
}
.BannerBoss .withAvatar .Avatar::after {
	box-shadow: inset 0 0 0 0 var(--image_border);
}
.BannerBoss .SimpleCell__main {
	padding-top: 9px;
	padding-bottom: 10px;
}
.BannerBoss .SimpleCell .Text, .BannerBoss .SimpleCell .Title {
	font-weight: 500;
	color: #000000;
}
.BannerBoss .SimpleCell .Caption {
	color: #8a7149;
}
.BannerBoss .BannerBossPerson {
	bottom: 0;
	position: absolute;
	-webkit-transform: scale(-1, 1) translate(25%, 50%);
	        transform: scale(-1, 1) translate(25%, 50%);
	width: auto;
	height: auto;
	z-index: 1;
}
.BannerBoss .BannerBossItem {
	bottom: 0;
	position: absolute;
	-webkit-transform: translate(15%, -15%);
	        transform: translate(15%, -15%);
	width: auto;
	height: auto;
	z-index: 1;
}
.BannerBoss .BannerBossBackground {
	background-size: cover;
	background-blend-mode: multiply;
	background-color: #9c9c9c;
	/* filter: blur(5px);
	padding: 20px;
	margin: -20px 0 0 -20px; */
}

.BannerBoss .Banner__actions {
	margin: 16px 0 0 auto;
	width: 60%;
	display: flex;
	flex-direction: column;
	padding: 8px;
	border-radius: 5px 0 0 0;
	background: #ecd6be;
	box-shadow: 0 0 0 3px #d0b9a3, 0 0 0 5px #5b4113, 0 0 0 11px #ffbc11, 0 0 0 13px #00000080;
}

.vkuiGroup .vkuiCardGrid:first-child {
	margin-top: 0;
}

.Persona.x2 .Avatar__children {
	-webkit-transform: scale(0.6) translateY(-24px);
	        transform: scale(0.6) translateY(-24px);
}
.GamePersona {
	width: 100%;
	height: 100%;
	position: absolute;
	background-position-x: center;
}

.CustomSelectOption {
	padding: 6px 9px;
}
.CustomSelectOption__before .Avatar {
	overflow: hidden;
	-webkit-transform: translate3d(0, 0, 0);
}
.CustomSelectOption__before .Avatar__img {
	-webkit-transform: scale(1.15);
	        transform: scale(1.15);
}


.CardsNews .vkuiLink {
	color: var(--text_primary);
}
.CardsNews .vkuiCard {
	/* background: transparent; */
}
.CardsNews .Link {
	width: 100%;
}
.CardsNews .HorizontalCell {
	display: block;
}
.CardsNews .HorizontalCell::before, .CardsNews .HorizontalCell::after {
	content: none;
}
.CardsNews .HorizontalCell__image {
	padding: 0;
	display: block;
}
.CardsNews .Avatar__img, .CardsNews .Avatar {
	border-radius: 10px!important;
    background-size: cover;
    background-position: center;
}
.CardsNews .HorizontalCell__content {
	padding: 8px;
}
.CardsNews .Avatar {
	width: auto!important;
}
.CardsNews .HorizontalCell__title, .CardsNews .HorizontalCell__subtitle {
	text-overflow: ellipsis;
    overflow: hidden;
	-webkit-transform: translate3d(0, 0, 0);
    white-space: nowrap;
}

.GalleryBanners .Gallery__slide:not(:last-child) {
	padding-right: 8px;
}

.BannerWiki .Text span, .BannerWiki .Title span {
	display: block;
	text-overflow: ellipsis;
    overflow: hidden;
	-webkit-transform: translate3d(0, 0, 0);
	font-size: 12px;
	opacity: .5;
}

.CardsNews .Avatar--ios {
	border-radius: 10px!important;
}
.CardsNews.withIcon .Card {
    background: transparent;
