.Skeleton {
	background-color: rgba(174, 183, 194, .12);
	background-color: var(--Skeleton_main);
	overflow: hidden;
	-webkit-transform: translate3d(0, 0, 0);
	position: relative;
	border-radius: 10px;
	width: 100%;
	height: 100%;
}
.Skeleton:after {
	-webkit-animation: Skeleton-loading 1.2s ease 0s infinite;
	        animation: Skeleton-loading 1.2s ease 0s infinite;
	background-image: linear-gradient(90deg, transparent 0%, rgba(174, 183, 194, .12) 50%, transparent 100%);
	background-image: linear-gradient(90deg, transparent 0%, var(--Skeleton_secondary) 50%, transparent 100%);
	content: "";
	height: 100%;
	left: 0;
	top: 0;
	position: absolute;
	-webkit-transform: translateX(-100%);
	        transform: translateX(-100%);
	width: 100%;
}
@-webkit-keyframes Skeleton-loading {
	to {
		-webkit-transform: translateX(100%);
		        transform: translateX(100%);
	}
}
@keyframes Skeleton-loading {
	to {
		-webkit-transform: translateX(100%);
		        transform: translateX(100%);
	}
}


